import time from "../../common/time.js";
export default {
	props:{
		item:Object,
		index:Number,
		userInfo:Object
	},
	methods:{
		comment(item){
			this.$emit("comSubimt",item)
		},
		deleteCom(item){
			this.$emit("comDelete",item)
			
		}
	},
	computed:{
		time(){
			let date = new Date(this.item.createTime).getTime()
			return time.gettime.gettime(date);
	}
},
}
